.frame-container {
    position: relative;
    width: 100%;
    display: inline-block;
    background-color: black;
}

.profile-frame {
    position: relative;
    width: 100%;
    /* Ajusta el tamaño según tu imagen */
    height: auto;
    display: block;
    z-index: 9999;
}



.info-container {
    position: absolute;
    top: 5%;
    display: inline-grid;
    grid-template-columns: 0.5fr 0.5fr;
    /* Divide en dos columnas iguales */
    align-items: center;
    /* Alinea verticalmente los elementos */
    width: 100%;
    height: 80%;
}

.info-container>div {
    display: flow;
    font-size: 30px;
    text-align: center;
    height: 100%;

}

.profile-image {
    background-color: #d41e1e;
    box-shadow: inset 4px 20px 20px 20px rgb(0 0 0 / 100%);
    width: 100%;
    height: 100%;
}

.profile-name {
    text-align: center;
    color: white;
    margin-top: 30px;
    font-weight: 500;
    font-size: 22px;
    font-family: 'Cinzel';
}

.profile-level-tag {
    color: white;
    margin-left: -30px;
    z-index: 1;
    font-size: 22px;
    font-family: 'Cinzel';
}

.profile-level {
    color: rgb(155, 18, 18);
    margin-left: 20px;
    z-index: 1;
    font-size: 36px;
    font-family: 'Reggae One';
}

.profile-id {
    position: absolute;
    top: 87%;
    left: 45%;
    width: 80%;
    color: white;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    font-size: 12px;
    font-family: 'cinzel';
    text-align: left;
}

.id-number {
    font-size: 16px;
}